.social{
  position: fixed;
  top:30%;
  z-index: 2000;

  a{
    display:flex;
    align-items: center;
    text-align: center;
    font-size: 20px;
    transition: all 300ms ease;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-decoration: none;
    color: rgb(22, 22, 22);
    transition: all 300ms ease;
    cursor: pointer;}

    #face{background-color: RGB(216,211,211);
    }

    #face:hover {
      background-color: RGB(72,103,170);
      box-shadow: inset 0 0 0 1px rgb(189, 190, 190);
      color: white;
    }
      
    #tw{
      background-color: RGB(216,211,211);}
    #tw:hover{
      background: RGB(29,161,242);
      box-shadow: inset 0 0 0 1px rgb(189, 190, 190);
      color: white;
    }
    
    #insta{
      background-color: RGB(216,211,211)
    }
    #insta:hover{
      background: linear-gradient(45deg,#405de6,#5851db,#833ab4,#c13584,#e1306c,#fd1d1d);
      box-shadow: inset 0 0 0 1px rgb(189, 190, 190);
      color: white;
    }
    
    #email{background-color: RGB(216,211,211);}
    #email:hover {
      background-color: rgb(20, 20, 20);
      box-shadow: inset 0 0 0 1px rgb(189, 190, 190);
      color: white;
    }
  }   
  

  


 