#today{
  display: none;
  font-family: "roboto";
  text-align: center;
  margin-bottom: 30px;
  color: rgb(0, 0, 0);
  width: 80%;
  height: auto;
  justify-content: center;
  border: 1px solid #E7E7EB;
  box-shadow: 10px 10px 5px 0px rgba(46, 46, 46, 0.75);
  border-radius: 5px;
  opacity:0.8;
}

@keyframes fadein{
  0%{opacity: 0 ;}
  100%{opacity:0.9;}
}