.details{
    display: grid;
    width: 95%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 15px;
    font-family:"roboto";
    font-size: 14px;
    border-top: 1px solid rgb(54, 53, 53);
    padding-top: 15px;
    .humidity{
        width: 100%;
        text-align: end;
      }
      .windspeed{
        width: 100%;
        text-align: start;
      }
      .pressure{
        width: 100%;
        text-align: end;
      }
      .feelslike{
        width: 100%;
        text-align: left;
    }
}
  
 