
@media(max-width:768px){
    #today{
      background-image: none;
      height: 100vh;
    }
}
  
@media (max-width:425px){
  body{
    background-image: linear-gradient(0, #a5d9f4 0, #6cb4d8 50%, #1d90bd 100%);
  }
  .div-container{
      width: 100%;
      margin-top: 15px;
      height: 60px;
     
    }
   
    .form{
      width: 90%;
      height: 60px;
      
    }
    
    .contain-input{width: 83%;
      #nameCity{width: 70%;}
     
    }
   
   #today{
      width: 90%;
      box-shadow: 7px 7px 4px 0px rgba(46, 46, 46, 0.75);
    }
    .details{width: 95%;}
    .btn-grados{margin-right: 10px;}
   
    #map{
      width: 95%;
      height: 150px;
      
    }
    
  .social{
     top: 50%;
     z-index: none;
     background: rgba(0, 0, 0, 0.5);
     border-top-right-radius: 4px;
     border-bottom-right-radius: 4px;
     width: 40px;
     height: 140px;
    
    #face{
      width: 25px;
      height: 25px;
      line-height: 25px;
      margin-bottom: 6px;
      margin-top: 12px;
      border-radius: 3px;}
     #tw{
      width: 25px;
      height: 25px;
      line-height: 25px;
      margin-bottom: 6px;
      border-radius: 3px
     } 
     #insta{
      width: 25px;
      height: 25px;
      line-height: 25px;
      margin-bottom: 6px;
      border-radius: 3px
     }
     #email{
      width: 25px;
      height: 25px;
      line-height: 25px;
      margin-bottom: 6px;
      border-radius: 3px
     }
   }
   
}