.div-container{
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  width: auto;
  height:60px;
  justify-content: center;

  .form{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        margin: 0px auto;
        height: 50px;
        
        .contain-input{
            border: 1px solid #E7E7EB;
            height: 38px;
            border-radius: 5px;
            background-color: rgb(39, 39, 39);
            opacity: 0.6;
            
            #icono{
            color:  #78797a;
            margin-left: 5px;
            }
            #nameCity{
            background-color: transparent;
            outline: none;
            border: none;
            line-height: 38px;
            color: snow;
            font-size: 15px;
            }
            #nameCity::placeholder{ 
            color: white;
            font-size: 14px;
            }
        }
        #button-search{
            width: 70px;
            height: 38px;
            padding: 6px;
            font-size: auto;
            margin-left: 12px;
            font-family: 'Arial Narrow Bold', sans-serif;
            background: rgb(216, 211, 211);
            font-weight: 600;
            border-radius: 5px;
            color: rgb(0, 0, 0);
            cursor: pointer;
            transition:all 300ms ease;
          }

        #button-search:hover{
        background: rgb(156, 155, 155);
        border-radius: 1px solid gray;
        color: white;
      }
    }
    
}







